@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url('SEGA.woff') format('woff');
}

h1{
    font-family: 'SEGA LOGO FONT';
    color: #FFA3D7;
}
.loading {
    margin-top: 49vh;
    display: flex;
    justify-content: center;
    align-items: center;
    animation-delay: 1s;
    text-align: center;
}

.loading .dot {
    position: relative;
    width: 2em;
    height: 2em;
    margin: 0.8em;
    border-radius: 50%;
}

.loading .dot::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: inherit;
    border-radius: inherit;
    animation: wave 2s ease-out infinite;
}

.loading .dot:nth-child(1) {
    background: #FCB1A6;
}

.loading .dot:nth-child(1)::before {
    animation-delay: 0.6s;
}

.loading .dot:nth-child(2) {
    background: #FB7476;
}

.loading .dot:nth-child(2)::before {
    animation-delay: 0.8s;
}

.loading .dot:nth-child(3) {
    background: #EF4D4D;
}

.loading .dot:nth-child(3)::before {
    animation-delay: 1s;
}

@keyframes wave {

    50%,
    75% {
        transform: scale(2.5);
    }

    80%,
    100% {
        opacity: 0;
    }
}
